import React from 'react'
import { Link } from 'gatsby'

import Layout from '../components/layout'
import Image from '../components/image'
import SEO from '../components/seo'


const IndexPage = () => (
	<Layout>
		<SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
		<p>Notes on Latex</p>
		<p> Download TextStudio from <a href="https://www.texstudio.org/"> here </a></p>

			<Image/>
	<div>
		Examples
		<ul>
			<li><a href="https://gist.github.com/jeffshadap/48b0ea69f5d133e10ec5d89fed6b3338"> Simple Title, Author </a></li>
			<li><a href="https://gist.github.com/jeffshadap/9d28ba7704b7420f94bea34cb3ab7b37">Abstract, Sections</a></li>
		</ul>
	</div>
	<Link to="/page-2/">Go to page 2</Link>
</Layout>
)

export default IndexPage
